li {
  list-style: none;
  text-decoration: none;
}

.order__container {
  margin-left: auto;
  margin-right: auto;

  @media screen and(min-width: 480px) {
    width: 480px;
  }

  @media screen and(min-width: 768px) {
    width: 768px;

  }

  @media screen and(min-width: 1280px) {
    width: 1200px;

  }

}

.order-container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;

  background-image: url(/src/images/order/background/Background_1-mob.jpg);

  @media screen and(min-width: 480px) {}

  @media screen and(min-width: 768px) {
    padding-left: 40px;
    padding-right: 40px;

    background-image: url(/src/images/order/background/Background_1-tab.jpg);

  }

  @media screen and(min-width: 1280px) {

    background-image: url(/src/images/order/background/Background_1.jpg);

  }

}

.order-title {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 28px;

  color: #FFFFFF;

  @media screen and(min-width: 480px) {
    padding-top: 40px;
  }

  @media screen and(min-width: 768px) {}

  @media screen and(min-width: 1280px) {}
}

.order-text {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 600;
  font-size: 32px;
  line-height: 38px;

  color: #FFFFFF;

  @media screen and(min-width: 480px) {
    margin-top: 35px;
    margin-bottom: 18px;
  }

  @media screen and(min-width: 768px) {}

  @media screen and(min-width: 1280px) {}
}

